//un reducer es una funcion que recibe el state y el action

import { types } from "../types/types";

// const state = {
// 	name: 'Ronald',
// 	logged: true
// }

export const authReducer = (state = {}, action) => {

	switch (action.type) {
		case types.login:
			return {
				...action.payload,
				logged: true
			}
		case types.logout:
			return {
				logged: false
			}

		default:
			return state;
	}

}